<template>
  <v-container>
    <h1>Registro diario</h1>
    <v-btn color="success" @click="createWimhof()">Crear nuevo wimHof</v-btn>
    <ul>
      <li></li>
    </ul>
  </v-container>
</template>

<script>
import Axios from "axios";
export default {
  data() {
    return {
      wimhofs: []
    };
  },
  mounted() {
    this.getWimhofs();
  },
  methods: {
    getWimhofs() {
      Axios.get("/api/wimhof")
        .then(response => {
          this.wimhofs = response.data.payload;
        })
        .catch(error => {
          // handle error
          console.log(error);
        });
    },
    createWimhof() {
      Axios.post("/api/wimhof", { date: Date.now() })
        .then(response => {
          this.wimhofs = response.data.payload;
        })
        .catch(error => {
          // handle error
          console.log(error);
        });
    }
  }
};
</script>

